//@import '../../common/styles/sass/mixins';

.project {
  composes: skill from '../../skills/skill/Skill.module.css';
  padding: 0;
  margin: 5px;
  position: relative;

  .projectImg {
    width: 250px;
    height: 250px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    &::before {
      position: absolute;
      content: '';
      background-color: black;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      opacity: 0.4;
    }

    &:hover::before {
      opacity: 0.1;
    }

    .viewMore {
      text-decoration: none;
      color: white;
      background-color: black;
      padding: 7px 25px;
      opacity: 0.85;
      position: relative;
      z-index: 1;
    }

  }
  .projectTitle {
    text-transform: uppercase;
    font-size: 18px;
  }

  .projectDescription {
    font-family: 'RobotoCondensed-Regular';
    font-size: 15px;
  }
}







