
.title {
    position: relative;
    padding: 5px;
    margin-bottom: 100px;
    text-transform: uppercase;
    font-size: 25px;
    .title h2 {
        margin: 40px 0 25px 0;
        color: black;
        text-shadow: white 1px 1px 0, white -1px -1px 0,
        white -1px 1px 0, white 1px -1px 0;
        letter-spacing: 13px;
    }
    &::after {
        content: '';
        position: absolute;
        width: 130px;
        height: 1.5px;
        background-color: white;
        left: 50%;
        transform: translate(-50%, 50%);
    }
}



